// 1, import
import styled from "styled-components";
import Link from "next/link";
const ReactMarkdown = require("react-markdown");
import Router from "next/router";
// import articles from "./article";
const article = require("./article");
const articles = article.articles;
import { withRouter } from "next/router";
console.log(articles);
const PRE_ONE = -1;
const NEXT_ONE = 1;
// var Remarkable = require("remarkable");
// var md = new Remarkable();

const getPageJson = router => {
  let pageJson = {};
  let id = 0;
  if (router.query && router.query.id) {
    id = router.query.id;
  }
  pageJson = articles.filter(item => {
    if (item.id == id) {
      return true;
    } else {
      return false;
    }
  });
  pageJson[0].sum = articles.length;
  return pageJson[0];
};
// 2, dom
const Index = props => {
  let data = { ...articles[0], ...{ sum: articles.length } };

  data = getPageJson(props.router);
  // console.log(page);
  const goTo = state => {
    let id = data.id + state;
    if (id < 0) id = 0;
    if (id >= data.sum) id = data.sum - 1;
    const as = "/cartoon/" + id;
    Router.push({ pathname: "/cartoon", query: { id } }, as);
  };

  return (
    <Container>
      <h2>题目：{data.title}</h2>
      <Tab>
        <button onClick={() => goTo(PRE_ONE)}>| &lt; </button>
        <span>
          位置：{data.id + 1}/{data.sum}
        </span>
        <button onClick={() => goTo(NEXT_ONE)}> &gt; | </button>
      </Tab>
      <Content>
        <ReactMarkdown source={data.content} />
        {/* {md.render(data.content)} */}
      </Content>
    </Container>
  );
};

export default withRouter(Index);

// 3,style
